export class Upload {
    $key: string;
    file:File = null;
    constructor(
        file:File,
        public name:string = "",
        public url:string = "",
        public progress: number = 0,
        private createdAt: Date = new Date()
    ) {
        this.file = file;
        console.log('new constructor');
    }

    public isEmptyFile(){
        return this.file == null;
    }
}
